@charset "UTF-8";
.block-content-grid {
  width: 100%;
  display: flex;
  background-color: rgb(var(--tertiary-color));
}
.block-content-grid__grid {
  width: 100%;
  min-height: fit-content;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .block-content-grid__grid {
    flex-flow: row wrap;
  }
}
.block-content-grid__grid .col-image {
  display: none;
}
@media (min-width: 768px) {
  .block-content-grid__grid .col-image {
    height: 650px;
    max-height: 650px;
    position: relative;
    display: flex;
    flex: 0 0 50%;
  }
  .block-content-grid__grid .col-image img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    object-position: center center;
  }
}
@media (min-width: 1441px) {
  .block-content-grid__grid .col-image {
    max-height: 100%;
    min-height: 100%;
    flex: 0 0 25%;
  }
}
.block-content-grid__grid .col-cite {
  padding: 70px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .block-content-grid__grid .col-cite {
    padding: 50px 20px;
    flex: 0 0 50%;
  }
}
@media (min-width: 1441px) {
  .block-content-grid__grid .col-cite {
    flex: 0 0 25%;
  }
}
.block-content-grid__grid .col-cite .quote {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.block-content-grid__grid .col-cite .quote .block-title {
  margin-bottom: 20px;
  text-align: center;
  color: rgb(var(--secondary-color));
}
.block-content-grid__grid .col-cite .quote:hover .cite::after {
  opacity: 1;
}
.block-content-grid__grid .col-cite .quote .cite {
  margin-bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  color: #d9d9d9;
  font-family: var(--roboto);
}
.block-content-grid__grid .col-cite .quote .cite q {
  margin-bottom: 50px;
  display: block;
}
.block-content-grid__grid .col-cite .quote .cite q::after, .block-content-grid__grid .col-cite .quote .cite q::before {
  content: none;
}
.block-content-grid__grid .col-cite .quote .cite::after {
  content: "“";
  width: fit-content;
  margin-top: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  opacity: 0.5;
  font-size: 200px;
  color: #fff;
  transition: opacity 0.3s linear 0s;
}
.block-content-grid__grid .col-cite .quote .photo {
  width: 95px;
  height: 95px;
  margin-bottom: 28px;
  display: block;
  border-radius: 50%;
}
.block-content-grid__grid .col-cite .quote .photo img {
  width: 100%;
  height: 100%;
  display: block;
  border-radius: 50%;
  object-fit: cover;
  object-position: center center;
}
.block-content-grid__grid .col-cite .quote .author {
  display: flex;
  flex-direction: column;
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  font-family: var(--roboto);
  text-transform: uppercase;
  text-align: center;
}
.block-content-grid__grid .col-cite .quote .author span {
  color: rgb(var(--secondary-color));
  font-size: 14px;
  text-transform: none;
}
.block-content-grid__grid .col-squares {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .block-content-grid__grid .col-squares {
    flex-flow: row wrap;
    flex: 0 0 100%;
  }
}
@media (min-width: 1441px) {
  .block-content-grid__grid .col-squares {
    flex: 0 0 50%;
  }
}
.block-content-grid__grid .col-squares .item {
  width: 100%;
  min-height: 435px;
  position: relative;
  display: block;
}
@media (min-width: 768px) {
  .block-content-grid__grid .col-squares .item {
    min-height: fit-content;
    flex: 0 0 50%;
  }
}
.block-content-grid__grid .col-squares .item .bg-img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  object-fit: cover;
  object-position: center center;
  z-index: 0;
}
.block-content-grid__grid .col-squares .item:nth-child(even) .item-wrapper {
  background-color: rgb(var(--secondary-color));
}
.block-content-grid__grid .col-squares .item:nth-child(even):hover .item-wrapper {
  background-color: rgb(var(--secondary-color), 0.8);
}
@media (min-width: 768px) {
  .block-content-grid__grid .col-squares .item:nth-child(3) .item-wrapper {
    background-color: rgb(var(--secondary-color));
  }
  .block-content-grid__grid .col-squares .item:nth-child(3):hover .item-wrapper {
    background-color: rgb(var(--secondary-color), 0.8);
  }
  .block-content-grid__grid .col-squares .item:nth-child(4) .item-wrapper {
    background-color: rgb(var(--primary-color));
  }
  .block-content-grid__grid .col-squares .item:nth-child(4):hover .item-wrapper {
    background-color: rgb(var(--primary-color), 0.8);
  }
}
.block-content-grid__grid .col-squares .item:hover .item-wrapper {
  background-color: rgb(var(--primary-color), 0.8);
}
@media (min-width: 768px) {
  .block-content-grid__grid .col-squares .item:hover .icon-wrapper {
    transform: matrix(1, 0, 0, 1, 0, -40.5);
  }
  .block-content-grid__grid .col-squares .item:hover .content-wrapper .heading {
    transform: matrix(1, 0, 0, 1, 0, -40.5);
  }
  .block-content-grid__grid .col-squares .item:hover .content-wrapper .heading .arrow {
    opacity: 0;
  }
  .block-content-grid__grid .col-squares .item:hover .content-wrapper .content {
    transform: translateY(0px);
    opacity: 1;
  }
}
.block-content-grid__grid .col-squares .item-wrapper {
  width: 100%;
  padding: 100px 40px;
  position: relative;
  z-index: 1;
  background-color: rgb(var(--primary-color));
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  transition: all 0.3s linear 0s;
}
@media (min-width: 1441px) {
  .block-content-grid__grid .col-squares .item-wrapper {
    padding: 70px 40px;
  }
}
.block-content-grid__grid .col-squares .item .icon-wrapper {
  width: 80px;
  height: 80px;
  margin-bottom: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .block-content-grid__grid .col-squares .item .icon-wrapper {
    transform: matrix(1, 0, 0, 1, 0, 0);
    transition: transform 0.4s;
  }
}
.block-content-grid__grid .col-squares .item .icon-wrapper img,
.block-content-grid__grid .col-squares .item .icon-wrapper svg {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: contain;
}
.block-content-grid__grid .col-squares .item .icon-wrapper img path,
.block-content-grid__grid .col-squares .item .icon-wrapper svg path {
  fill: #fff;
}
.block-content-grid__grid .col-squares .item .content-wrapper {
  width: 100%;
  position: relative;
}
.block-content-grid__grid .col-squares .item .content-wrapper .heading {
  margin-bottom: 20px;
  position: relative;
  font-size: 2.4rem;
  line-height: normal;
  font-weight: 400;
  color: #fff;
  text-align: center;
}
@media (min-width: 768px) {
  .block-content-grid__grid .col-squares .item .content-wrapper .heading {
    padding-bottom: 40px;
    transform: matrix(1, 0, 0, 1, 0, 0);
    transition: transform 0.4s;
  }
}
.block-content-grid__grid .col-squares .item .content-wrapper .arrow {
  display: none;
}
@media (min-width: 768px) {
  .block-content-grid__grid .col-squares .item .content-wrapper .arrow {
    content: "";
    width: 24px;
    height: 20px;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    display: block;
  }
  .block-content-grid__grid .col-squares .item .content-wrapper .arrow path {
    fill: #fff;
  }
}
.block-content-grid__grid .col-squares .item .content-wrapper .content {
  margin-bottom: 0;
  text-align: center;
  color: #fff;
}
@media (min-width: 768px) {
  .block-content-grid__grid .col-squares .item .content-wrapper .content {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    transform: translateY(-20px);
    opacity: 0;
    transition: all 0.3s linear 0s;
  }
}